.hero {
  display: flex;
  font-family: "Roboto", sans-serif;
  margin: 0 auto;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  min-height: 500px;
  max-width: 1100px;
  height: 1000px;
  padding: 99px 0 0;

  &__name {
    margin: 0 0 30px 4px;
    color: #64ffda;
    font-size: 20px;
    font-weight: 400;
    font-family: "Inter", sans-serif;
  }

  &__big-heading {
    margin: 5px 0;
    color: #ccd6f6;
    line-height: 1.1;
    font-weight: 600;
    font-size: 60px;
  }

  &__desc {
    margin: 20px 0;
    font-family: "Roboto", sans-serif;
    font-size: 20px;
    color: #8892b0;
    line-height: 1.3;
  }

  &__button {
    display: inline-block;
    margin: 50px 0 0;
    font-family: "Inter", sans-serif;
    padding: 1.25rem 1.75rem;
    border: 1px solid #64ffda;
    border-radius: 4px;
    font-size: 14px;
    background-color: transparent;
    color: #64ffda;
    transition: all 1s;

    &:hover {
      background-color: #046552;
    }
  }

  @media (max-width: 999px) {

    height: 650px;

    &__name {
      font-size: 15px;
      margin: 0 0 8px 4px;
    }

    &__big-heading {
      font-size: 35px;
    }

    &__desc {
      font-size: 15px;
    }

    &__button {
      @include fontParams(14px, 10px, 500);
      margin-top: 20px;
      padding: 1rem;
    }
  }
}
