.menu {
  display: none;
  position: fixed;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 6;

  &__inner {
    background-color: #0f2c59;
    opacity: .9;
    border-radius: 15px;
  }

  &__links {
    display: flex;
    padding: 15px 10px;
  }

  &__link {
    @include fontParams (18px, 100%, 400);
    margin: 0 5px;
    color: #64ffda;

    & > span {
      color: #fff;

      &:hover {
        color: #64ffda;
      }
    }
  }

  @media (max-width: 999px) {
    display: flex;
  }
}
